"use client";
import { Doughnut } from "react-chartjs-2";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";

ChartJS.register(ArcElement, Tooltip, Legend);

export default function CategoryDonut({ byCategory }: { byCategory: Record<string, number> }) {
  const labels = Object.keys(byCategory);
  const values = Object.values(byCategory);

  const professionalPalette = [
    "#1f77b4", // blue
    "#ff7f0e", // orange
    "#2ca02c", // green
    "#d62728", // red
    "#9467bd", // purple
    "#8c564b", // brown
    "#e377c2", // pink
    "#7f7f7f", // grey
    "#bcbd22", // olive
    "#17becf", // teal
  ];

  const data = {
    labels,
    datasets: [
      {
        data: values,
        backgroundColor: professionalPalette.slice(0, labels.length),
        borderColor: "#1f2937", // Tailwind gray-800 border for dark bg
        borderWidth: 2,
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    cutout: "70%",
    plugins: {
      legend: {
        position: "top" as const,
        labels: {
          usePointStyle: true,
          color: "#e5e7eb", // Tailwind gray-200 for dark mode
        },
      },
      tooltip: {
        bodyColor: "#f9fafb", // text inside tooltip (gray-50)
        backgroundColor: "#111827", // tooltip bg (gray-900)
        borderColor: "#374151", // tooltip border (gray-700)
        borderWidth: 1,
      },
    },
  };

  return (
    <div className="max-w-sm mx-auto p-4 rounded-lg bg-gray-900 shadow-md">
      <div style={{ height: 300, width: 300 }}>
        <Doughnut data={data} options={options} />
      </div>
    </div>
  );
}
